import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, NavController, ToastController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { User } from '../../providers';
import { Settings, Api } from '../../providers';
import { Device } from '@ionic-native/device';

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html'
})
export class SignupPage {
  // The account fields for the login form.
  // If you're using the username field with or without email, make
  // sure to add it to the type
  account: {
    email: string,
    username: string,
    password: string,
    firstname: string,
    lastname: string,
    phone: number,
    birthday: Date,
    adress: string,
    city: string,
    zipcode: string,
    is_regular: boolean,
    is_special_event: boolean,
    is_senior_service: boolean
  } = {
    email: '',
    username: '',
    password: '',
    firstname: '',
    lastname: '',
    phone: null,
    birthday: null,
    adress: '',
    city: '',
    zipcode: '',
    is_regular: true,
    is_special_event: true,
    is_senior_service: true
  };

  private isEmailValid: boolean = true;
  private alreadyvalidated: boolean = false;
  private isUsernameValid: boolean = true;
  private isPasswordValid: boolean = true;
  private isCityValid: boolean = true;
  private isZipcodeValid: boolean = true;
  private isFirstnameValid: boolean = true;
  private isLastnameValid: boolean = true;
  private isPhoneValid: boolean = true;
  private isAdressValid: boolean = true;
  private isBirthdayValid: boolean = true;
  private errorEmailexist: string;
  private errorUsernameexist: string;
  private errorEmail: string;
  private errorUsername: string;
  private emailFormatError: string;
  private mandatory: string;
  private regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  private lang: string;
  private pushid: any;

  // Our translated text strings
  private signupErrorString: string;

  constructor(public navCtrl: NavController,
    public user: User,
    public toastCtrl: ToastController,
    public translateService: TranslateService,
    private storage: Storage,
    public settings: Settings,
    public api: Api,
    public device: Device,
    public alertCtrl: AlertController) {
      this.lang = null;

      this.storage.get('_settings')
      .then((settings) => {
        if(settings==null){
         this.lang = 'fr';
        }else{
          this.lang = settings.language;
        }
      });
      this.settings.load().then(() => {
      });

    this.translateService.get(['SIGNUP_ERROR', 'REQUIRED_FIELD', 'INVALIDEMAIL', 'EMAILEXIST', 'USERNAMEEXIST']).subscribe((res) => {
      this.signupErrorString = res.SIGNUP_ERROR;
      this.mandatory = res.REQUIRED_FIELD;
      this.emailFormatError = res.INVALIDEMAIL;
      this.errorEmailexist = res.EMAILEXIST;
      this.errorUsernameexist = res.USERNAMEEXIST;
    })
  }

  doSignup() {
    // Attempt to login in through our User service
    var valid = this.validate();
    if (valid) {
      var data: any = this.account;
      data.lang = this.lang;
      
      data.platform = this.device.platform;
      this.storage.get('pushid')
      .then((pushid) => {
        if( pushid !==null) {
          data.pushid = pushid;
          let confirmAlert = this.alertCtrl.create({
            title: 'notif',
            message: JSON.stringify(data),
            buttons: [{
              text: 'Ignorer',
              role: 'cancel'
            }]
          });
          confirmAlert.present();
          }
          let seq = this.api.post('subscribe', data).share();
    
          seq.subscribe((res: any) => {
            if (res.error == true) {
              if (res.message == 'emailexist') {
                this.errorEmail = this.errorEmailexist;
                this.isEmailValid = false;
              }
              if (res.message == 'usernameexist') {
                this.errorUsername = this.errorUsernameexist;
                this.isUsernameValid = false;
              }
            } else {
    
            }
          }, err => {
            console.error('ERROR', err);
          });
        
      })
      
    }
    
  }

  validate():boolean {
    this.isEmailValid = true;
    this.isLastnameValid = true;
    this.isFirstnameValid = true;
    this.isUsernameValid = true;
    this.isPasswordValid = true;
    this.isCityValid = true;
    this.isAdressValid = true;
    this.isZipcodeValid = true;
    this.isPhoneValid = true;
    this.isBirthdayValid = true;
    this.errorEmail = this.mandatory;
    this.errorUsername = this.mandatory;
    this.alreadyvalidated = true;

    if (!this.account.username || this.account.username.length == 0) {
        this.isUsernameValid = false;
    }
    if (!this.account.email || this.account.email.length == 0) {
      this.isEmailValid = false;
    }
    if (!this.account.firstname || this.account.firstname.length == 0) {
      this.isFirstnameValid = false;
    }
    if (!this.account.lastname || this.account.lastname.length == 0) {
      this.isLastnameValid = false;
    }

    if (!this.account.password || this.account.password.length == 0) {
        this.isPasswordValid = false;
    }

    if (!this.account.city || this.account.city.length == 0) {
        this.isCityValid = false;
    }

    if (!this.account.zipcode || this.account.zipcode.length == 0) {
        this.isZipcodeValid = false;
    }
    if (!this.account.adress || this.account.adress.length == 0) {
      this.isAdressValid = false;
    }
    if (!this.account.phone || this.account.phone.toString().length == 0) {
      this.isPhoneValid = false;
    }

    if (!this.account.birthday) {
      this.isBirthdayValid = false;
    }

    if (!this.account.email || this.account.email.length == 0) {
      this.isEmailValid = false;
    } else if (this.regex.test(this.account.email) == false) {
      this.isEmailValid = false;
      this.errorEmail = this.emailFormatError;
    }

    return this.isEmailValid &&
        this.isPasswordValid &&
        this.isUsernameValid &&
        this.isCityValid &&
        this.isZipcodeValid &&
        this.isAdressValid &&
        this.isFirstnameValid &&
        this.isLastnameValid &&
        this.isBirthdayValid &&
        this.isPhoneValid;
  }
  changeLanguage(lang) {
    this.settings.setValue('language', lang);
    this.translateService.use(lang);
    this.lang = lang;
    this.translateService.get(['SIGNUP_ERROR', 'REQUIRED_FIELD', 'INVALIDEMAIL', 'EMAILEXIST', 'USERNAMEEXIST']).subscribe((res) => {
      this.signupErrorString = res.SIGNUP_ERROR;
      this.mandatory = res.REQUIRED_FIELD;
      this.emailFormatError = res.INVALIDEMAIL;
      this.errorEmail = res.EMAILEXIST;
      this.errorUsername = res.USERNAMEEXIST;
    });
    if (this.alreadyvalidated) {
      this.validate();
    }
  }
}



